.auth {
  position: relative;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  gap: 5rem;
  margin: 6rem auto;
  width: 40rem;
  max-width: 90%;
  border-radius: 1px;
  background-image: url("../../assets/login.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  padding: 2rem;
  z-index: 3;
  text-align: left;
}

.auth h1 {
  text-align: center;
  color: var(--color-golden);
  font-size: 10rem;
  text-transform: uppercase;
}

.auth figure {
  position: absolute;
  inset: 0;
  top: 0%;
  width: 90%;
  height: 100%;
  left: -3%;
  z-index: -1;
}

.auth figure img {
  width: 90%;
}
.control {
  margin-bottom: 3rem;
}

.auth form {
  height: 20rem;
  overflow: scroll;
}
.control label {
  display: block;
  color: var(--color-white);
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.control input {
  font: var(--font-alt);
  background-color: white;
  color: var(--color-black);
  border-radius: 2px;
  border: none;
  width: 30rem;
  text-align: left;
  padding: 0.75rem;
}
.control input::placeholder {
  font: var(--font-alt);
  color: rgba(0, 0, 0, 0.274);
}

.actions {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.actions button {
  cursor: pointer;
  font: inherit;
  color: var(--color-gray);
  background-color: var(--color-crimson);
  border: none;
  outline: none;
  border-radius: 2px;
  padding: 0.5rem 2.5rem;
}

.actions button:hover {
  background-color: var(--color-golden);
}

.actions .toggle {
  margin-top: 1rem;
  background-color: transparent;
  color: var(--color-white);
  border: none;
  padding: 0.15rem 1.5rem;
}

.actions .toggle:hover {
  background-color: transparent;
  color: var(--color-golden);
}

.order {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.2rem;
  margin: 1rem 0;
}
.button-orderSuccess {
  font: inherit;
  text-align: right;
  cursor: pointer;
  background-color: var(--color-golden);
  color: white;
  padding: 0.5rem 2rem;
  border-radius: 2px;
  border: none;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.616);
  margin-left: 1rem;
}

.button-orderSuccess:hover,
.button-orderSuccess:active {
  background-color: #0e3013;
  border-color: #0e3013;
  color: white;
}

.invalid p {
  color: #ca3e51;
}

.invalid input {
  border-color: #aa0b20;
  background-color: #ffeff1;
}

button:disabled,
button:disabled:hover,
button:disabled:active {
  cursor: not-allowed;
  background-color: #ccc;
  color: #3f3f3f;
  border-color: #ccc;
}

@media screen and (min-width: 2001px) {
  .auth figure {
    width: 110%;
    left: -10%;
  }
  .auth h1 {
    font-size: 8rem;
    line-height: 9rem;
    text-transform: uppercase;
  }
  .invalid p {
    color: #ca3e51;
    margin-top: 0.5rem;
    font-weight: 600;
    background: rgba(255, 255, 255, 0.377);
    font-size: 2rem;
    display: inline-block;
  }
}
@media screen and (max-width: 2000px) {
  .auth figure {
    width: 100%;
    left: -5%;
  }
  .auth h1 {
    font-size: 8rem;
    line-height: 9rem;
    text-transform: uppercase;
  }
  .invalid p {
    color: #ca3e51;
    margin-top: 0.5rem;
    line-height: 1.5;
    font-weight: 600;
    background: rgba(255, 255, 255, 0.377);
    font-size: 2rem;
    display: inline-block;
    padding: 0.5rem;
  }
}
@media screen and (max-width: 1150px) {
  .auth figure {
    width: 100%;
    top: -10%;
    left: 1%;
  }

  .invalid p {
    color: #ca3e51;
    margin-top: 0.5rem;
    line-height: 1.5;
    font-weight: 600;
    background: rgba(255, 255, 255, 0.377);
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 450px) {
  .auth h1 {
    font-size: 7rem;
    line-height: 8rem;
    text-transform: uppercase;
  }
  .auth figure {
    width: 110%;
    top: -10%;
    left: -5%;
  }
  .auth {
    width: 90%;
  }
}

@media screen and (max-width: 395px) {
  .control input {
    width: 26rem;
  }
}

@media screen and (max-width: 375px) {
  .control input {
    width: 23rem;
  }
  .control label {
    font-size: 1.8rem;
  }

  .auth h1 {
    font-size: 5.5rem;
    line-height: 6rem;
    text-transform: uppercase;
  }

  .auth figure {
    width: 110%;
    top: -10%;
    left: -5%;
  }
  .auth form {
    height: 19rem;
    overflow: scroll;
  }
  .invalid p {
    color: #ca3e51;
    margin-top: 0.5rem;
    line-height: 1.5;
    font-weight: 600;
    background: rgba(255, 255, 255, 0.377);
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 350px) {
  .control input {
    width: 23rem;
  }
  .control label {
    font-size: 1.8rem;
  }

  .auth h1 {
    font-size: 5.5rem;
    line-height: 6rem;
    text-transform: uppercase;
  }

  .auth figure {
    width: 110%;
    top: -10%;
    left: -5%;
  }
}
@media screen and (max-width: 320px) {
  .control input {
    width: 21rem;
  }
  .control label {
    font-size: 1.8rem;
  }

  .auth h1 {
    font-size: 5rem;
    line-height: 6rem;
    text-transform: uppercase;
  }

  .auth figure {
    width: 110%;
    top: -10%;
    left: -5%;
  }
}

@media screen and (max-width: 295px) {
  .control input {
    width: 18rem;
  }
  .invalid p {
    font-size: 1.2rem;
  }
  .auth h1 {
    font-size: 4.5rem;
    line-height: 5rem;
  }
}
